import AxiosInstance from '../../utils/AxiosInstance'
import React, { useEffect, useState } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import { Row, Col, Form, Input, Select, Tag, Button, DatePicker, Space, Radio, message, Spin, Typography, Popconfirm, List, Card, Checkbox, Image } from 'antd'
import * as anticons from '@ant-design/icons'
import toast from 'react-hot-toast'
import dayjs from 'dayjs'
import moment from 'moment'

import TrainingService from '../../service/TrainingService'
import PostulantService from '../../service/PostulantService'
import CourseService from '../../service/CourseService'
import { getCatalogos, getDatosUsuario, getFormatoFecha } from '../../utils/Utils'
import { sendEmailRegister } from '../../utils/urls'

const trainingService = new TrainingService()
const postulantService = new PostulantService()
const courseService = new CourseService()
const { Option } = Select

const Training = () => {

    const navigate = useHistory()
    const { id } = useParams()
    const [load, setLoad] = useState(true)
    const [loadCourse, setLoadCourse] = useState(true)
    const [loadPostulant, setLoadPostulant] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [isLoadingCourse, setIsLoadingCourse] = useState(false)
    const [isLoadingPostulant, setIsLoadingPostulant] = useState(false)
    const [isSaving, setIsSaving] = useState(false)
    const [isPublishing, setIsPublishing] = useState(false)
    const [isSendingEmail, setIsSendingEmail] = useState(false)
    const [training, setTraining] = useState(null)
    const [postulants, setPostulants] = useState([])
    const [courses, setCourses] = useState([])
    const [error, setError] = useState('')
    const [selectedModality, setSelectedModality] = useState(0)

    const renderPaisValor = e => <Option key={e.catValor} value={e.catId}>
        <>
            <img
                src={`https://flagcdn.com/24x18/${e.catDescripcion.toLowerCase()}.png`}
                srcSet={`https://flagcdn.com/48x36/${e.catDescripcion}.png 2x,https://flagcdn.com/72x54/${e.catDescripcion}.png 3x`}
                width="24"
                height="18"
                alt={e.catNombre}></img>
            &nbsp;{
                e.catNombre
            }
        </>
    </Option>

    const renderModality = ({ catId, catNombre, catValor }) =>
        <Radio.Button
            key={catId}
            value={catId}
            style={{
                height: '100%',
                margin: '0 20px 20px 0',
            }}
            onChange={e => setSelectedModality(catId)}
        >
            <Row>
                <Col style={{
                    padding: '20px 20px',
                    borderRadius: '15px',
                }}>
                    <Image preview={false} src={`./../../assets/${catValor}${catId === selectedModality ? '_W' : ''}.png`} width={55} height={50} />
                </Col>
            </Row>
            {catNombre}
        </Radio.Button>

    const renderApplicants = ({ posId, posEmail, posEnvio }) =>
        <Col key={posEmail}>
            <Tag key={posEmail} closeIcon={<anticons.CloseOutlined />} onClose={(e) => { delPostulant({ posId }) }}
                closable
                style={{
                    padding: '5px 15px',
                    borderRadius: '15px',
                    fontSize: '1em'
                }}
            >
                {
                    posEnvio === true ? <><anticons.SendOutlined />&nbsp;</> : <></>
                }
                {posEmail}
            </Tag>
        </Col>

    const setCourse = async (course) => {
        setIsSaving(true)
        const newCourse = { ...course, idEntrenamiento: training.entId }
        if (course.checked) {
            try {
                const { data } = await courseService.put({ navigate, course: newCourse })
                if (data) {
                    setError(null)
                    fetchCourses()
                } else {
                    setError("An error occurred.")
                }
            } catch (error) {
                const { response } = error
                setError(response.status === 409 ? response.data[0].messageText : "An error occurred.")
            } finally {
                setIsSaving(false)
            }
        } else {
            try {
                const { data } = await courseService.del({ navigate, course: newCourse })
                if (data) {
                    setError(null)
                    fetchCourses()
                } else {
                    setError("An error occurred.")
                }
            } catch (error) {
                const { response } = error
                setError(response.status === 409 ? response.data[0].messageText : "An error occurred.")
            } finally {
                setIsSaving(false)
            }
        }
    }

    const setPostulant = async (values) => {
        setIsSaving(true)
        const postulant = { ...values, idEntrenamiento: training.entId, idInvitadoPor: getDatosUsuario().usuarioId }
        try {
            const { data } = await postulantService.put({ navigate, postulant })
            if (data) {
                setError(null)
                fetchPostulants()
            } else {
                setError("An error occurred.")
            }
        } catch (error) {
            const { response } = error
            setError(response.status === 409 ? response.data[0].messageText : "An error occurred.")
        } finally {
            setIsSaving(false)
        }
    }

    const delPostulant = async (values) => {
        setIsSaving(true)
        const postulant = { ...values, idEntrenamiento: training.entId, idInvitadoPor: getDatosUsuario().usuarioId }
        try {
            const { data } = await postulantService.del({ navigate, postulant })
            if (data) {
                setError(null)
                fetchPostulants()
            } else {
                setError("An error occurred.")
            }
        } catch (error) {
            const { response } = error
            setError(response.status === 409 ? response.data[0].messageText : "An error occurred.")
        } finally {
            setIsSaving(false)
        }
    }

    const fetchData = async () => {
        setLoad(false)
        setIsLoading(true)
        const training = { entId: id }
        try {
            const { data } = await trainingService.getById({ navigate, training })
            if (data) {
                const training = data.listObject[0]
                setError(null)
                setTraining({
                    ...training,
                    entFechaIni: moment(dayjs(training.entFechaIni.substring(0, 10), getFormatoFecha()).$d),
                    entFechaFin: moment(dayjs(training.entFechaFin.substring(0, 10), getFormatoFecha()).$d)
                })
                setSelectedModality(training.entModalidad.catId)
            } else {
                setError("An error ocurred")
            }
        } catch (error) {
            const { response } = error
            setError(response.status === 409 ? response.data[0].messageText : "An error ocurred")
        } finally {
            setIsLoading(false)
        }
    }

    const fetchCourses = async () => {
        setLoadCourse(false)
        setIsLoadingCourse(true)
        const training = { idEntrenamiento: id }
        try {
            const { data } = await courseService.getByTraining({ navigate, training })
            if (data) {
                const courses = data.listObject
                setError(null)
                setCourses(courses)
            } else {
                setError("An error ocurred")
            }
        } catch (error) {
            const { response } = error
            setError(response.status === 409 ? response.data[0].messageText : "An error ocurred")
        } finally {
            setIsLoadingCourse(false)
        }
    }

    const fetchPostulants = async () => {
        setLoadPostulant(false)
        setIsLoadingPostulant(true)
        const training = { idEntrenamiento: id }
        try {
            const { data } = await postulantService.getByTraining({ navigate, training })
            if (data) {
                const postulants = data.listObject
                setError(null)
                setPostulants(postulants)
            } else {
                setError("An error ocurred")
            }
        } catch (error) {
            const { response } = error
            setError(response.status === 409 ? response.data[0].messageText : "An error ocurred")
        } finally {
            setIsLoadingPostulant(false)
        }
    }

    const save = async (values) => {
        toast.success(`Training ${training.entNombre} update succesfully`)
        // // const training = {
        // //     ...values,
        // //     idEstado: getCatalogos().trainingStatus.filter(s => s.catValor === 'DRAFT')[0].catId,
        // //     idUsuario: getDatosUsuario().usuarioId
        // // }
        // setIsSaving(true)
        // // try {
        // //     const { data } = await trainingService.put({ navigate, training })
        // //     if (data) {
        // //         setError(null)
        // // Asignar cursos
        // const requests = []
        // if (courses.length > 0) {
        //     courses.forEach(p => {
        //         requests.push(courseService.put({
        //             navigate, course: {
        //                 "idEntrenamiento": training.entId,
        //                 "idNombreCurso": p.catId
        //             }
        //         }))
        //     });
        // }
        // // Asignar postulantes
        // if (training.postulanteList.length > 0) {
        //     training.postulanteList.filter(p => p.posId === undefined || p.posId === null).forEach(p => {
        //         requests.push(postulantService.put({
        //             navigate, postulant: {
        //                 "idEntrenamiento": training.entId,
        //                 "posEmail": p.posEmail,
        //                 "idInvitadoPor": getDatosUsuario().usuarioId
        //             }
        //         }))
        //     });
        // }
        // if (requests.length > 0) {
        //     Promise.all(requests)
        //         .then((results) => {
        //             toast.success(`Training ${training.entNombre} update succesfully`)
        //             // navigate.push('/training')
        //             setLoad(true)
        //         }).catch(error => {
        //             const { response } = error
        //             const msgError = response.status === 409 ? response.data[0].messageText : response.data
        //             message.error(`It was not possible to add the applicant: ${msgError}`)
        //         })
        //     // } else {
        //     //     toast.success(`Training ${data.listObject[0].entNombre} update succesfully`)
        //     //     // navigate.push('/training')
        //     //     setLoad(true)
        //     // }
        //     //     } else {
        //     //         setError("An error occurred.")
        //     //     }
        //     // } catch (error) {
        //     //     const { response } = error
        //     //     setError(response.status === 409 ? response.data[0].messageText : "An error occurred.")
        //     // } finally {
        //     //     setIsSaving(false)
        // }
    }

    const pub = async () => {
        if (courses.length < 1 || postulants.length < 1) {
            toast.error('Add at least one course and one applicant')
            return;
        }
        const modTraining = {
            entId: training.entId,
            idEstado: getCatalogos().trainingStatus.filter(s => s.catValor === 'ACTIVE')[0].catId
        }
        setIsPublishing(true)
        try {
            const { data } = await trainingService.pub({ navigate, training: { ...modTraining } })
            if (data) {
                setError(null)
                // Notificar postulantes
                if (postulants.length > 0) {
                    const envios = postulants.map(p => async function () {
                        return postulantService.sendEmail({ navigate, postulant: { posId: p.posId } })
                    })
                    setIsSendingEmail(true)
                    envios
                        .reduce((promise, fn) =>
                            promise.then(fn)
                                .then(r => {
                                    const { data } = r
                                    if (data === false)
                                        toast.error('Could not send email')
                                    else
                                        toast.success('Email sent successfully')
                                })
                                .catch(({ response }) => {
                                    toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred. Could not send email.")
                                })
                            , Promise.resolve())
                        .then(_ => {
                            setIsSendingEmail(false)
                            toast.success(`Training ${data.listObject[0].entNombre} publish succesfully`)
                            navigate.push('/training')
                        })
                } else {
                    toast.success(`Training ${data.listObject[0].entNombre} publish succesfully`)
                    navigate.push('/training')
                }
            }
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred.")
        } finally {
            setIsPublishing(false)
        }
    }

    useEffect(() => {
        if (load) {
            fetchData()
        }
        if (loadCourse) {
            fetchCourses()
        }
        if (loadPostulant) {
            fetchPostulants()
        }
    })

    return (
        <>
            {
                isLoading ?
                    <Row style={{ padding: '30px 10px' }}>
                        <Col span={24} style={{ textAlign: 'center' }}>
                            <Spin size='large' tip={
                                <Typography.Paragraph style={{ padding: '10px 0' }}>
                                    Loading...
                                </Typography.Paragraph>
                            } />
                        </Col>
                    </Row>
                    :
                    training && <>
                        <Form
                            layout='vertical'
                            initialValues={
                                training
                            }
                            onFinish={save}
                            disabled={isSaving}
                        >
                            <div className="grid">
                                <div className="col-12">
                                    <div className="mgeCard">
                                        <div className="grid">
                                            <div className="col-4">
                                                <Space direction='horizontal'>
                                                    <Button icon={<i className="fa-solid fa-chevron-left"></i>}
                                                        onClick={() => {
                                                            navigate.push('/training')
                                                        }}
                                                    ></Button>
                                                    <h3 style={{ padding: '0', margin: '0' }} className='mge-darkGreen'>
                                                        Edit Training
                                                    </h3>
                                                </Space>
                                            </div>
                                            <div className="col-8" style={{ textAlign: 'right' }}>
                                                <Space>
                                                    <Button
                                                        type='default'
                                                        htmlType='submit'
                                                        loading={isSaving}
                                                        disabled={isSaving || training.entEstado.catValor === 'ACTIVE'}
                                                    >Save Draft</Button>
                                                    <Popconfirm title="Are you sure to publish the training?" okText="Yes, publish" cancelText="No" onConfirm={() => pub()}
                                                        okButtonProps={{
                                                            loading: isPublishing || isSendingEmail,
                                                        }}
                                                    >
                                                        <Button
                                                            type='primary'
                                                            loading={isPublishing || isSendingEmail}
                                                            disabled={(isPublishing || isSendingEmail) || training.entEstado.catValor === 'ACTIVE'}
                                                        >Publish</Button>
                                                    </Popconfirm>
                                                </Space>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <Row justify='center'>
                                <Col xs={22} sm={22} md={20} lg={18} xl={18} xxl={16}>
                                    <Row gutter={[25, 25]}>
                                        <Col xs={24} sm={8} md={8} lg={8} xl={8} xxl={8}>
                                            <Form.Item
                                                name={['entPais', 'catId']}
                                                label="Country"
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: 'Select country',
                                                    },
                                                ]}
                                            >
                                                <Select>
                                                    {
                                                        getCatalogos().paises.map(p => renderPaisValor(p))
                                                    }
                                                </Select>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={16} md={16} lg={16} xl={16} xxl={16}>
                                            <Form.Item
                                                name='entNombre'
                                                label="Training name"
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: 'Complete training name',
                                                    },
                                                ]}
                                            >
                                                <Input disabled />
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Row gutter={[25, 25]}>
                                        <Col span={24}>
                                            <Form.Item
                                                name={['entModalidad', 'catId']}
                                                label="Modality"
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: 'Select modality',
                                                    },
                                                ]}
                                                style={{ width: '100%', textAlign: 'center' }}
                                            >
                                                <Radio.Group buttonStyle='solid'>
                                                    {
                                                        getCatalogos().modalidades.map(m => renderModality(m))
                                                    }
                                                </Radio.Group>
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Row gutter={[25, 25]}>
                                        <Col span={12}>
                                            <Form.Item
                                                name='entFechaIni'
                                                label="Start Date"
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: 'Select start date',
                                                    },
                                                ]}
                                            >
                                                <DatePicker style={{ width: '100%' }} />
                                            </Form.Item>
                                        </Col>
                                        <Col span={12}>
                                            <Form.Item
                                                name='entFechaFin'
                                                label="End Date"
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: 'Select end date',
                                                    },
                                                ]}
                                            >
                                                <DatePicker style={{ width: '100%' }} />
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                </Col>
                            </Row>
                        </Form>
                        <Row justify='center'>
                            <Col xs={22} sm={22} md={20} lg={18} xl={18} xxl={16}>
                                <Typography.Paragraph>
                                    <Typography.Text type='danger'>*</Typography.Text>&nbsp;Courses
                                </Typography.Paragraph>
                                {
                                    <List
                                        itemLayout='horizontal'
                                        grid={{ gutter: 15, column: 2 }}
                                        dataSource={training.cursoList}
                                        renderItem={({ curNombre }) => (
                                            <List.Item>
                                                <Card style={{ textAlign: 'center' }}>
                                                    <Checkbox
                                                        checked
                                                    >
                                                        {curNombre.catNombre}
                                                    </Checkbox>
                                                </Card>
                                            </List.Item>
                                        )}
                                    />
                                }
                                {/* {
                                    getCatalogos().cursos &&
                                    <List
                                        itemLayout='horizontal'
                                        grid={{ gutter: 15, column: 2 }}
                                        dataSource={getCatalogos().cursos}
                                        renderItem={(item) => (
                                            <List.Item>
                                                <Card style={{ textAlign: 'center' }}>
                                                    <Checkbox
                                                        disabled={isSaving || isLoadingCourse}
                                                        checked={
                                                            courses.filter(c => c.curNombre.catId === item.catId).length > 0
                                                        }
                                                        onChange={(e) => {
                                                            setCourse({
                                                                checked: e.target.checked,
                                                                idNombreCurso: item.catId,
                                                                curId: courses.filter(c => c.curNombre.catId === item.catId).length > 0 ? courses.filter(c => c.curNombre.catId === item.catId)[0].curId : 0
                                                            })
                                                        }}>
                                                        {item.catNombre}
                                                    </Checkbox>
                                                </Card>
                                            </List.Item>
                                        )}
                                    />
                                } */}
                            </Col>
                        </Row >
                        <Row justify='center'>
                            <Col xs={22} sm={22} md={20} lg={18} xl={18} xxl={16}>
                                <Row>
                                    <Form layout='vertical' style={{ width: '100%' }}
                                        onFinish={setPostulant}
                                        initialValues={
                                            {
                                                posEmail: ''
                                            }
                                        }
                                        disabled={isSaving || isLoadingCourse}
                                    >
                                        <Col span={24}>
                                            <Form.Item
                                                name={['posEmail']}
                                                label='Applicants emails'
                                                extra='Press enter to add email'
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: 'Complete applicants',
                                                    },
                                                ]}
                                            >
                                                <Input />
                                            </Form.Item>
                                        </Col>
                                        <Col span={24}>
                                            <Row gutter={[15, 15]}>
                                                {
                                                    postulants && postulants.map(a => renderApplicants(a))
                                                }
                                            </Row>
                                        </Col>
                                    </Form>
                                </Row>
                            </Col>
                        </Row >
                    </>
            }
        </>
    )
}

export default Training