import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { Row, Col, Typography, Button, Badge, Space, Spin, Result } from 'antd'

import TrainingService from '../../service/TrainingService'
import { getDatosUsuario } from '../../utils/Utils'

const trainingService = new TrainingService()

export const Courses = () => {
    const navigate = useHistory()
    const [courses, setCourses] = useState([])
    const [load, setLoad] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [error, setError] = useState(null)

    const renderCourse = ({ entId, entNombre, entFechaIni, entFechaFin, entModalidad, numPostulantes, entEstado }) => {
        const color = entEstado.catValor === 'ACTIVE' ? '#54873C' : entEstado.catValor === 'INACTIVE' ? 'red' : '#636466'
        return (
            <Col key={entId} xs={24} sm={12} md={12} lg={12} xl={8} xxl={8}>
                <div className='card'>
                    <Row style={{ lineHeight: '1.8em' }}>
                        <Col span={16}>
                            <Typography.Title level={5}>
                                <span className='mge-darkGreen' style={{ fontWeight: 'bold' }}>
                                    {entNombre}
                                </span>
                            </Typography.Title>
                        </Col>
                        <Col span={8} style={{ textAlign: 'right' }}>
                            <Badge color={color} text={entEstado.catNombre} />
                        </Col>
                        <Col span={24}>
                            <Typography.Text>
                                {entModalidad.catNombre}
                            </Typography.Text>
                        </Col>
                        <Col span={24}>
                            <Typography.Text strong>
                                {entFechaIni.substring(0, 10).split('-').reverse().join('/')} - {entFechaFin.substring(0, 10).split('-').reverse().join('/')}
                            </Typography.Text>
                        </Col>
                        <Col span={24}>
                            <Typography.Text type='secondary'>
                                {numPostulantes} applicants
                            </Typography.Text>
                        </Col>
                    </Row>
                    <Row>
                        <Col span={24}>&nbsp;</Col>
                    </Row>
                    <Row gutter={[20]}>
                        <Col span={12}>
                            <Button type='default' block
                                onClick={() => {
                                    navigate.push(`/training/${entId}`)
                                }}
                                style={
                                    {
                                        borderColor: '#70B450',
                                        borderWidth: '2px',
                                        color: '#70B450',
                                        height: '40px'
                                    }
                                }
                            >
                                View
                            </Button>
                        </Col>
                        <Col span={12}>
                            <Button type='default' block
                                onClick={() => {
                                    navigate.push(`/trainingedit/${entId}`)
                                }}
                                style={
                                    {
                                        borderColor: '#70B450',
                                        borderWidth: '2px',
                                        color: '#70B450',
                                        height: '40px'
                                    }
                                }
                            >
                                Edit
                            </Button>
                        </Col>
                    </Row>
                </div>
            </Col>
        )
    }

    const fetchData = async () => {
        setLoad(false)
        setIsLoading(true)
        setCourses([])
        const trainer = { idUsuario: getDatosUsuario().usuarioId }
        try {
            const { data } = await trainingService.getByTraining({ navigate, trainer })
            if (data) {
                setError(null)
                setCourses(data.listObject)
            } else {
                setError("Hubo un error al obtener las ofertas de invesión")
            }
        } catch (error) {
            const { response } = error
            setError(response.status === 409 ? response.data[0].messageText : "No pudimos hacer la solicitud para obtener las ofertas de invesión")
        } finally {
            setIsLoading(false)
        }
    }

    useEffect(() => {
        if (load) {
            fetchData()
        }
    })

    return (
        <>
            <div className="grid">
                <div className="col-12">
                    <div className="mgeCard">
                        <div className="grid">
                            <div className="col-4">
                                <h3 style={{ padding: '0', margin: '0' }} className='mge-darkGreen'>
                                    Training
                                </h3>
                            </div>
                            <div className="col-8" style={{ textAlign: 'right' }}>
                                <Space>
                                    <Button
                                        type='default'
                                        loading={isLoading}
                                        disabled={isLoading}
                                        onClick={() => {
                                            fetchData()
                                        }}>Refresh</Button>
                                    <Button
                                        type='primary'
                                        disabled={isLoading}
                                        onClick={() => {
                                            navigate.push('/trainingnew')
                                        }}>New Training</Button>
                                </Space>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {
                isLoading ?
                    <Row style={{ padding: '30px 10px' }}>
                        <Col span={24} style={{ textAlign: 'center' }}>
                            <Spin size='large' tip={
                                <Typography.Paragraph style={{ padding: '10px 0' }}>
                                    Loading...
                                </Typography.Paragraph>
                            } />
                        </Col>
                    </Row>
                    :
                    error ? <>
                        <Result
                            title={error}
                        />
                    </> :
                        <Row gutter={[25, 25]} justify='center'>
                            {
                                courses && courses.map(c => renderCourse(c))
                            }
                        </Row>
            }
        </>
    )
}
