import React from 'react'
import { Layout, Row, Col, Typography } from 'antd'

const Classes = () => {
    return (
        <>
            <Row>
                <Col span={24}>
                    <div style={
                        {
                            backgroundColor: '#ffffff',
                            padding: '15px 20px 6px 20px',
                            borderRadius: '15px',
                            marginBottom: '20px'
                        }
                    }>
                        <Row>
                            <Col>
                                <Row>
                                    <Col>
                                        <Typography.Title level={3} type='secondary'>
                                            Classes
                                        </Typography.Title>
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    </div>
                </Col>
            </Row>
        </>
    )
}

export default Classes