import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { Row, Col, Typography, Button, Space, Upload, Spin, Image, Form, Input, Descriptions, Tooltip } from 'antd'
import * as anticons from '@ant-design/icons'
import * as apiURLs from '../../utils/urls'
import { getCatalogos, getDatosUsuario, getToken, getTypeContent } from '../../utils/Utils'
import $ from 'jquery'
import { saveAs } from 'file-saver'

import PostulantService from '../../service/PostulantService'
import InformService from './../../service/InformService'
import EvidenceService from '../../service/EvidenceService'
import toast from 'react-hot-toast'

const { Dragger } = Upload

const postulantService = new PostulantService()
const informService = new InformService()
const evidenceService = new EvidenceService()

const FinalReport = () => {

    const navigate = useHistory()
    const [postulant, setPostulant] = useState({})
    const [load, setLoad] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [isDownloading, setIsDownloading] = useState(false)

    const [fileList, setFileList] = useState([]);
    const [uploading, setUploading] = useState(false)
    const [selectedFinalReport, setSelectedFinalReport] = useState(0)

    const downloadFinalReportTemplate = () => {

        const metaData = 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'

        var xhrOverride = new XMLHttpRequest();
        xhrOverride.responseType = 'arraybuffer';

        setIsDownloading(true)
        $.ajax({
            async: true,
            crossDomain: true,
            url: apiURLs.downloadFinalReport(),
            method: 'GET',
            headers: {
                Accept: "*/*",
                Authorization: `Bearer ${getToken()}`
            },
            xhr: function () {
                return xhrOverride;
            }
        }).then(function (response) {
            setIsDownloading(false)
            const blob = new Blob([response], { type: metaData })
            saveAs(blob, "FinalReportTemplate.docx")
        });
    }

    const downloadInform = ({ infNombre, infRutaPlan }) => {

        const metaData = getTypeContent(infNombre)

        var xhrOverride = new XMLHttpRequest();
        xhrOverride.responseType = 'arraybuffer';

        setIsDownloading(true)
        $.ajax({
            async: true,
            crossDomain: true,
            url: apiURLs.downloadInformByName(),
            method: 'POST',
            headers: {
                Accept: "*/*",
                Authorization: `Bearer ${getToken()}`,
                "Content-Type": "application/json"
            },
            data: JSON.stringify({
                infNombre,
                infRutaPlan
            }),
            xhr: function () {
                return xhrOverride;
            }
        }).then(function (response) {
            setIsDownloading(false)
            const blob = new Blob([response], { type: metaData })
            saveAs(blob, infNombre)
        });
    }

    const renderFinalReport = ({ infId, infFechaReg, infFechaSAprRec, infNombre, infEstado, infObservacion, infRutaPlan, evidenciaList }) =>
        <div
            key={infId}
            style={
                {
                    backgroundColor: '#ffffff',
                    padding: '15px',
                    borderRadius: '15px',
                    marginBottom: '20px'
                }
            }>
            <Row>
                <Col xs={12} sm={12} md={12} lg={12} xl={12} xxl={12}>
                    <Row gutter={10}>
                        <Col>
                            <Image src={`./../../assets/${infEstado.catValor === 'EST_INF_ENV' ? 'send.png' : infEstado.catValor === 'EST_INF_APR' ? 'accept.png' : infEstado.catValor === 'EST_INF_REC' ? 'cancel.png' : 'minus.png'}`} preview={false} width={24} height={24} />
                        </Col>
                        <Col>
                            <Typography.Title level={5}>
                                {infFechaReg.substring(0, 10)}
                            </Typography.Title>
                        </Col>
                        {/* {
                            infFechaSAprRec && <Col>
                                <Typography.Title level={5}>
                                    {infFechaSAprRec.substring(0, 10)}
                                </Typography.Title>
                            </Col>
                        } */}
                        <Col>
                            <Typography.Title level={5}>
                                {infNombre}
                            </Typography.Title>
                        </Col>
                    </Row>
                </Col>
                <Col xs={12} sm={12} md={12} lg={12} xl={12} xxl={12}>
                    <Row gutter={10} justify='end'>
                        <Col style={{ textAlign: 'end' }}>
                            <Space>
                                <Button type='default'
                                    onClick={() => downloadInform({ infNombre, infRutaPlan })}
                                    loading={isDownloading}
                                    disabled={isDownloading}
                                >
                                    Download
                                </Button>
                                <Button type='text'
                                    onClick={(e) => setSelectedFinalReport(infId === selectedFinalReport ? 0 : infId)}
                                    icon={
                                        selectedFinalReport !== infId ? <anticons.ArrowDownOutlined /> : <anticons.ArrowUpOutlined />
                                    }
                                />
                            </Space>
                        </Col>
                    </Row>
                </Col>
            </Row>
            {
                selectedFinalReport === infId &&
                <Descriptions
                    size='small'
                    layout='vertical'
                    column={
                        {
                            xs: 2,
                            sm: 2,
                            md: 4,
                            lg: 4,
                            xl: 6,
                            xxl: 6,
                        }
                    }
                >
                    <Descriptions.Item label={
                        <Typography.Text type='secondary' strong>Registration date</Typography.Text>
                    }
                    >
                        {infFechaReg.substring(0, 10)}
                    </Descriptions.Item>

                    {
                        evidenciaList && evidenciaList.length > 0 ?
                            <Descriptions.Item label={
                                <Typography.Text type='secondary' strong>Evidences</Typography.Text>
                            }
                            >
                                <a rel="noreferred noreferrer" target='_blank' href={`${evidenciaList[0].eviUrl}`}>
                                    {evidenciaList[0].eviUrl}
                                </a>
                            </Descriptions.Item>
                            :
                            <>
                            </>
                    }

                    {
                        infFechaSAprRec !== undefined && infFechaSAprRec !== null ?
                            <Descriptions.Item label={
                                <Typography.Text type='secondary' strong>Review data</Typography.Text>
                            }
                            >
                                {infFechaSAprRec.substring(0, 10)}
                            </Descriptions.Item>
                            : <></>
                    }

                    <Descriptions.Item label={
                        <Typography.Text type='secondary' strong>Observation</Typography.Text>
                    }
                    >
                        {infObservacion}
                    </Descriptions.Item>

                </Descriptions>
            }
        </div>

    const handleUpload = async (values) => {
        const inform = new FormData();
        fileList.forEach((file) => {
            inform.append('file', file)
            inform.append('idTipo', getCatalogos().reportType.filter(t => t.catValor === 'INFORME_FINAL')[0].catId)
            inform.append('idCurso', postulant.entId.cursoList[0].curId)
            inform.append('idPostulante', postulant.posId)
        });
        setUploading(true)
        try {
            const { data } = await informService.put({ navigate, inform })
            const informInserted = data.listObject[0]
            const { infId } = informInserted
            const newInform = { ...values, infId }
            sendFinalReport(newInform)
            toast.success('Upload successfully')
        } catch (_) {
            toast.error('Upload failed')
        } finally {
            setUploading(false)
            setFileList([])
        }
    }

    const props = {
        onRemove: (file) => {
            const index = fileList.indexOf(file);
            const newFileList = fileList.slice();
            newFileList.splice(index, 1);
            setFileList(newFileList);
        },
        beforeUpload: (file) => {
            setFileList([...fileList, file]);
            return false;
        },
        fileList,
    }

    const sendFinalReport = async (values) => {
        const { url, infId } = values
        const evidence = {
            idInforme: infId,
            eviUrl: url
        }
        setIsLoading(true)
        try {
            const { data } = await evidenceService.put({ navigate, evidence })
            toast.success('Evidence sended successfully')
            setLoad(true)
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
        } finally {
            setIsLoading(false)
        }
    }

    const fetchData = async () => {
        setLoad(false)
        setIsLoading(true)
        try {
            const postulant = { idUsuario: getDatosUsuario().usuarioId }
            const { data } = await postulantService.getTraining({ navigate, postulant })
            const training = data.listObject[0]
            setPostulant(training)
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : 'An error occurred')
        } finally {
            setIsLoading(false)
        }
    }

    useEffect(() => {
        if (load) fetchData()
    })

    return (
        <>
            <Row>
                <Col span={24}>
                    <div style={
                        {
                            backgroundColor: '#ffffff',
                            padding: '15px 20px 6px 20px',
                            borderRadius: '15px',
                            marginBottom: '20px'
                        }
                    }>
                        <Row>
                            <Col xs={24} sm={24} md={12} lg={12} xl={12} xxl={12}>
                                <Row>
                                    <Col>
                                        <Typography.Title level={3} type='secondary'>
                                            Final Report template
                                        </Typography.Title>
                                    </Col>
                                </Row>
                            </Col>
                            <Col xs={24} sm={24} md={12} lg={12} xl={12} xxl={12}>
                                <Row justify='end'>
                                    <Space direction='horizontal'>
                                        <Col>
                                            <Button type='default'
                                                style={
                                                    {
                                                        borderColor: '#70B450',
                                                        borderWidth: '2px',
                                                        color: '#70B450',
                                                        height: '35px'
                                                    }
                                                }
                                                onClick={() => downloadFinalReportTemplate()}
                                                loading={isDownloading}
                                                disabled={isDownloading}
                                            >
                                                Download
                                            </Button>
                                        </Col>
                                        {/* <Col>
                                            <Button type='default'
                                                style={
                                                    {
                                                        borderColor: '#70B450',
                                                        borderWidth: '2px',
                                                        color: '#70B450',
                                                        height: '35px'
                                                    }
                                                }
                                                onClick={() => downloadFinalReportTemplate()}
                                                loading={isDownloading}
                                                disabled={isDownloading}
                                            >View</Button>
                                        </Col> */}
                                    </Space>
                                </Row>
                            </Col>
                        </Row>
                    </div>
                </Col>
                <Col span={24}>
                    {
                        postulant.informeList && postulant.informeList.filter(f => f.infTipo.catValor === 'INFORME_FINAL').map(f => renderFinalReport(f))
                    }
                </Col>
            </Row>

            {
                isLoading ?
                    <Row style={{ padding: '30px 10px' }}>
                        <Col span={24} style={{ textAlign: 'center' }}>
                            <Spin size='large' tip={
                                <Typography.Paragraph style={{ padding: '10px 0' }}>
                                    Loading...
                                </Typography.Paragraph>
                            } />
                        </Col>
                    </Row>
                    :
                    postulant && postulant.informeList && <>
                        {
                            postulant.informeList.filter(w => w.infTipo.catValor === 'PLAN_TRABAJO' &&
                                w.infEstado.catValor === 'EST_INF_APR'
                            ).length > 0
                                &&
                                (
                                    postulant.informeList.filter(w => w.infTipo.catValor === 'INFORME_FINAL').length < 1
                                    ||
                                    (
                                        postulant.informeList.filter(w => w.infTipo.catValor === 'INFORME_FINAL')[postulant.informeList.filter(w => w.infTipo.catValor === 'INFORME_FINAL').length - 1].infEstado.catValor === 'EST_INF_REC'
                                    )
                                )
                                ?
                                <Form
                                    onFinish={handleUpload}
                                    autoComplete='off'
                                    layout='vertical'
                                >
                                    <Row>
                                        <Col span={24}>
                                            <Typography.Title level={5}>
                                                Upload your final report
                                            </Typography.Title>
                                        </Col>
                                        <Col span={24}>
                                            <Form.Item
                                                name='file'
                                                label='File'
                                                rules={[
                                                    {
                                                        required: true
                                                    }
                                                ]}
                                            >
                                                <Upload
                                                    multiple={false}
                                                    {...props}
                                                >
                                                    <Button
                                                        icon={<anticons.UploadOutlined />}
                                                        disabled={
                                                            fileList.length > 0
                                                        }
                                                    >Select File</Button>
                                                </Upload>
                                            </Form.Item>
                                        </Col>
                                        <Col span={24} style={{ paddingTop: '10px' }}>
                                            <Typography.Title level={5}>
                                                Copy evidence link
                                            </Typography.Title>
                                        </Col>
                                        <Col span={24}>
                                            <Form.Item
                                                name='url'
                                                label='URL'
                                                rules={[
                                                    {
                                                        required: true,
                                                    },
                                                    {
                                                        type: 'url',
                                                        warningOnly: true,
                                                    },
                                                    {
                                                        type: 'string',
                                                        min: 6,
                                                    },
                                                ]}
                                            >
                                                <Input />
                                            </Form.Item>
                                        </Col>
                                        <Col span={24}>
                                            <Button
                                                type='primary'
                                                htmlType='submit'
                                                disabled={fileList.length === 0}
                                                loading={uploading}
                                            >
                                                {uploading ? 'Uploading' : 'Send'}
                                            </Button>
                                        </Col>
                                    </Row>
                                </Form>
                                : <>
                                </>
                        }
                    </>

            }

            {/* <Row>
                <Col span={24}>
                    <Typography.Title level={5}>
                        Upload your final report
                    </Typography.Title>
                </Col>
                <Col span={24}>
                    <div style={
                        {
                            backgroundColor: '#ffffff',
                            padding: '15px',
                            borderRadius: '15px',
                            marginBottom: '20px'
                        }
                    }>
                        <Dragger {...props}>
                            <p className="ant-upload-drag-icon">
                                <anticons.InboxOutlined />
                            </p>
                            <p className="ant-upload-text">Click or drag file to this area to upload</p>
                        </Dragger>
                    </div>
                </Col>
                <Col span={24}>
                    <Typography.Title level={5}>
                        Copy evidence link
                    </Typography.Title>
                </Col>
                <Col span={24}>
                    <Form.Item>
                        <Input />
                    </Form.Item>
                </Col>
                <Col span={24} style={{ textAlign: 'end' }}>
                    <Button type='primary'>Send</Button>
                </Col>
            </Row> */}
        </>
    )
}

export default FinalReport