import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { Row, Col, Typography, Button, Spin, Image, Divider, Result } from 'antd'
import { getDatosUsuario } from '../../utils/Utils'
import toast from 'react-hot-toast'

import PostulantService from '../../service/PostulantService'
import CertificateService from '../../service/CertificateService'

const postulantService = new PostulantService()
const certificateService = new CertificateService()


const Certificate = () => {

    const navigate = useHistory()
    const [postulant, setPostulant] = useState({})
    const [load, setLoad] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [trigger, setTrigger] = useState(false)
    const [isGenerating, setIsGenerating] = useState(false)
    const [isDownloading, setIsDownloading] = useState(false)

    const generate = async () => {
        setTrigger(false)
        setIsGenerating(true)
        try {
            const course = {
                idCurso: postulant.entId.cursoList[0].curId,
                idPostulante: postulant.posId
            }
            const { data } = await certificateService.generate({ navigate, course })
            console.log(data)
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : 'An error occurred')
        } finally {
            setIsGenerating(false)
        }
    }

    const fetchData = async () => {
        setLoad(false)
        setIsLoading(true)
        try {
            const postulant = { idUsuario: getDatosUsuario().usuarioId }
            const { data } = await postulantService.getTraining({ navigate, postulant })
            const training = data.listObject[0]
            setPostulant(training)
            setTrigger(true)
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : 'An error occurred')
        } finally {
            setIsLoading(false)
        }
    }

    useEffect(() => {
        if (load) fetchData()
        if (trigger) generate()
    })

    return (
        <>
            {
                isLoading || isGenerating ?
                    <Row style={{ padding: '30px 10px' }}>
                        <Col span={24} style={{ textAlign: 'center' }}>
                            <Spin size='large' tip={
                                <Typography.Paragraph style={{ padding: '10px 0' }}>
                                    Loading...
                                </Typography.Paragraph>
                            } />
                        </Col>
                    </Row>
                    :
                    postulant && postulant.informeList && postulant.certificadoList &&
                    <>
                        {
                            postulant.informeList.filter(w => w.infTipo.catValor === 'INFORME_FINAL' &&
                                w.infEstado.catValor === 'EST_INF_APR'
                            ).length > 0
                                &&
                                postulant.certificadoList.length >= 0
                                ?
                                <>
                                    <Row justify='center'>
                                        <Col xs={24} sm={24} md={24} lg={24} xl={20} xxl={14}>
                                            <div style={
                                                {
                                                    backgroundColor: '#ffffff',
                                                    padding: '40px 45px',
                                                    borderRadius: '15px',
                                                    marginBottom: '20px'
                                                }
                                            }
                                            >
                                                <Row justify='center'
                                                    style={{
                                                        position: 'relative'
                                                    }}>
                                                    <Col span={12}>
                                                        <Image src='./../../assets/logo.png' />
                                                    </Col>
                                                    <Col span={12}>
                                                    </Col>
                                                    <Col span={24} style={{ padding: '15px 40px', textAlign: 'center' }}>
                                                        <Typography.Title level={2} style={{ textAlign: 'center' }}>
                                                            <span className='mge-green'>
                                                                {
                                                                    postulant && postulant.entId &&
                                                                    <>
                                                                        {
                                                                            `CERTIFICADO DE FACILITADOR/A DEL CURSO ${postulant.entId.cursoList[0].curNombre.catNombre}`
                                                                        }
                                                                    </>
                                                                }
                                                            </span>
                                                        </Typography.Title>
                                                        <Typography.Paragraph className='mge-darkGreen' strong>
                                                            Otorgado a:
                                                        </Typography.Paragraph>
                                                    </Col>
                                                    <Col span={18} style={{ padding: '0 20px 0 20px', textAlign: 'center' }}>
                                                        <Typography.Paragraph className='mge-darkGreen'>
                                                            <span style={{
                                                                fontStyle: 'italic',
                                                                fontFamily: 'cursive',
                                                                fontSize: '2em',
                                                                color: '#666666'
                                                            }}>
                                                                {
                                                                    postulant && postulant.usuId &&
                                                                    <>
                                                                        {
                                                                            `${postulant.usuId.nombres} ${postulant.usuId.apellidos}`
                                                                        }
                                                                    </>
                                                                }
                                                            </span>
                                                            <Divider style={{ borderWidth: '1px', borderColor: '#385A28' }} />
                                                        </Typography.Paragraph>
                                                    </Col>
                                                    <Col span={20} style={{ padding: '0 40px', textAlign: 'center' }}>
                                                        <Typography.Paragraph className='mge-darkGreen' strong>
                                                            {
                                                                postulant && postulant.entId &&
                                                                <>
                                                                    {
                                                                        `Al haber cumplido con satisfacción la aplicación del curso Enverdeciendo mi negocio del programa My Green Enterprise, desarrollado desde el ${postulant.entId.entFechaIni.substring(0, 10)} al ${postulant.entId.entFechaFin.substring(0, 10)}.`
                                                                    }
                                                                </>
                                                            }
                                                        </Typography.Paragraph>
                                                    </Col>
                                                    <Col span={24}>
                                                        <Row justify='center'>
                                                            <Col span={8} style={{ textAlign: 'center' }}>
                                                                <Divider style={{ borderWidth: '1px', borderColor: '#333333', padding: '0 0', margin: '40px 0 0 0' }} />
                                                                Firma
                                                            </Col>
                                                            <Col span={4}></Col>
                                                            <Col span={8} style={{ textAlign: 'center' }}>
                                                                <Divider style={{ borderWidth: '1px', borderColor: '#333333', padding: '0 0', margin: '40px 0 0 0' }} />
                                                                Firma
                                                            </Col>
                                                        </Row>
                                                    </Col>
                                                    <div className='mgeBG'
                                                        style={{
                                                            position: 'absolute',
                                                            bottom: '-40px',
                                                            right: '-45px',
                                                            width: '100%',
                                                            height: '100vh',
                                                            opacity: '0.1'
                                                        }}
                                                    >
                                                        &nbsp;
                                                    </div>
                                                </Row>
                                            </div>
                                        </Col>
                                    </Row>
                                    {/* <Row>
                                        <Col span={24} style={{ textAlign: 'center' }}>
                                            <Button type='primary'>Download</Button>
                                        </Col>
                                    </Row> */}
                                </>
                                :
                                <>
                                    <Result
                                        title="You will see your certificate once your final report is approved"
                                        extra={
                                            <Button type="primary" key="console"
                                                onClick={() => {
                                                    navigate.push('/finalreport')
                                                }}
                                            >
                                                Send Final Report
                                            </Button>
                                        }
                                    />
                                </>
                        }
                    </>
            }
        </>
    )
}

export default Certificate