import React, { useState } from "react"
import { Link, useHistory, useLocation } from "react-router-dom"
import { Input, Form, Button, Row, Col, Divider, Layout, Image, Spin, Typography, Result, Alert } from 'antd'
import PasswordChecklist from 'react-password-checklist'
import AlertWait from "./AlertWait"
import Logo from "../components/Logo"
import md5 from 'md5'
import CuentaService from "../service/CuentaService"

const cuentaService = new CuentaService()

const ResetPassword = () => {

    const navigate = useHistory()
    const { search } = useLocation()
    const { id, uuid } = JSON.parse('{"' + search.substring(1, search.length).replace(/&/g, '","').replace(/=/g, '":"') + '"}', function (key, value) { return key === "" ? value : decodeURIComponent(value) })
    const [isLoading, setIsLoading] = useState(false)
    const [error, setError] = useState(null)
    const [okMsg, setOkMsg] = useState(null)

    const [form] = Form.useForm()

    const [validPostulant, setValidPostulant] = useState({
        posEstado: {
            catValor: 'EST_INV'
        }
    })

    const [password, setPassword] = useState("")
    const [passwordConfirm, setPasswordConfirm] = useState("")

    const reset = (values) => {
        setError(null)
        setOkMsg(null)
        setIsLoading(true)
        cuentaService
            .updatePassword({ datos: { usuId: id, uuid, password: md5(values.password), passwordConfirm: md5(values.passwordConfirm) } })
            .then((response) => {
                setIsLoading(false)
                // setError('Your password was reset succesfully. Please log in again.')
                setOkMsg(<>
                    Your password was reset succesfully. Please click <Link to='/login'>here</Link> log in again.
                </>)
            })
            .catch((error) => {
                setIsLoading(false)
                const { response } = error
                if (response.status === 500) {
                    setError(response.statusText)
                } else if (response.status === 404) {
                    setError("The requested resource is not available or cannot be found.")
                } else {
                    setError(response.data[0].messageText)
                }
            })
    }

    return (
        <>
            <Row>
                <Col span={24}>
                    <div className="notification" style={{
                        borderRadius: '0px',
                        textAlign: 'center'
                    }}>
                        <Image alt='' src='./../assets/logo.png' preview={false} />
                    </div>
                </Col>
            </Row>

            <Layout style={{
                padding: '40px 20px',
                backgroundColor: '#F9F9F9'
            }}>
                <div className='mgeBG' style={{ opacity: '0.3', height: '100vh', position: 'fixed', width: '100%', bottom: '0', right: '0' }}>&nbsp;</div>
                {
                    validPostulant &&
                    <>
                        {
                            validPostulant.posEstado.catValor === 'EST_INV' ?
                                <Row justify='center'>
                                    <Col xs={24} sm={18} md={16} lg={12} xl={8} xxl={6}>
                                        <Divider><span className="mge-darkGreen" style={{ textTransform: 'uppercase', letterSpacing: '1px' }}>Reset password</span></Divider>
                                        <Row>
                                            <Col span={24}>
                                                <Form
                                                    layout={'vertical'}
                                                    form={form}
                                                    onFinish={reset}
                                                >
                                                    <Form.Item label="New Password"
                                                        name='password'
                                                        rules={[
                                                            {
                                                                required: true,
                                                                message: 'Complete new password',
                                                            },
                                                        ]}
                                                    >
                                                        <Input.Password onChange={e => setPassword(e.target.value)} />
                                                    </Form.Item>
                                                    <Form.Item label="Confirm new password"
                                                        name='passwordConfirm'
                                                        rules={[
                                                            {
                                                                required: true,
                                                                message: 'Confirme new password',
                                                            },
                                                        ]}
                                                    >
                                                        <Input.Password onChange={e => setPasswordConfirm(e.target.value)} />
                                                    </Form.Item>
                                                    <Form.Item>
                                                        <PasswordChecklist
                                                            rules={["minLength", "specialChar", "number", "capital", "match"]}
                                                            minLength={8}
                                                            value={password}
                                                            valueAgain={passwordConfirm}
                                                            messages={{
                                                                minLength: "Password has more than 8 characters.",
                                                                specialChar: "Password has special characters.",
                                                                number: "Password has a number.",
                                                                capital: "Password has a capital letter.",
                                                                match: "Passwords match.",
                                                            }}
                                                            onChange={(isValid) => { }}
                                                        />
                                                    </Form.Item>

                                                    {
                                                        error &&
                                                        <Alert
                                                            message=''
                                                            description={error}
                                                            type='warning'
                                                            showIcon
                                                            closable
                                                            banner
                                                        />
                                                    }

                                                    {
                                                        okMsg &&
                                                        <Alert
                                                            message=''
                                                            description={okMsg}
                                                            type='success'
                                                            showIcon
                                                            closable
                                                            banner
                                                        />
                                                    }
                                                    <div style={{
                                                        paddingTop: '10px'
                                                    }}>
                                                        <Form.Item>
                                                            <Button
                                                                type='primary'
                                                                htmlType='submit'
                                                                loading={isLoading}
                                                                disabled={isLoading}
                                                                block>Reset Password</Button>
                                                        </Form.Item>
                                                    </div>
                                                </Form>
                                            </Col>
                                        </Row>
                                        {/* <AlertWait error={error} isLoading={isLoading} /> */}
                                    </Col>
                                </Row>
                                :
                                <Result
                                    title="Your account has been previously created"
                                    extra={
                                        <Button type="primary" key="console"
                                            onClick={() => {
                                                navigate.push('/login')
                                            }}
                                        >
                                            Log in
                                        </Button>
                                    }
                                />
                        }
                    </>
                }
            </Layout>
        </>
    )

}

export default ResetPassword