import React, { useEffect, useState } from 'react';
import { Route, withRouter, useLocation } from 'react-router-dom';
import App from './App';
// import { Error } from './pages/Error';
// import { NotFound } from './pages/NotFound';
// import { Access } from './pages/Access';
// import Register from './pages/Register';
import RecuperarPassword from './pages/RecuperarPassword';
import ResetPassword from './pages/ResetPassword';
// import RegisterPYME from './pages/RegisterPersona';
// import Help from './pages/Help';
import Signup from './pages/Signup';
import Login from './pages/Login';

export const ctx = React.createContext()

const AppWrapper = (props) => {
	let location = useLocation();

	useEffect(() => {
		window.scrollTo(0, 0)
	}, [location]);

	const [colorScheme, setColorScheme] = useState('light')

	switch (props.location.pathname) {
		case '/login':
			return <Route path="/login" render={() => <Login colorScheme={colorScheme} />} />
		// case '/register':
		// 	return <Route path="/register" render={() => <Register colorScheme={colorScheme} />} />
		case '/signup':
			return <Route path="/signup" render={() => <Signup colorScheme={colorScheme} />} />
		// case '/registerpyme':
		// 	return <Route path="/registerpyme" render={() => <RegisterPYME colorScheme={colorScheme} />} />
		case '/recover':
			return <Route path="/recover" render={() => <RecuperarPassword colorScheme={colorScheme} />} />
		case '/reset':
				return <Route path="/reset" render={() => <ResetPassword colorScheme={colorScheme} />} />
		// case '/error':
		// 	return <Route path="/error" render={() => <Error colorScheme={colorScheme} />} />
		// case '/notfound':
		// 	return <Route path="/notfound" render={() => <NotFound colorScheme={colorScheme} />} />
		// case '/access':
		// 	return <Route path="/access" render={() => <Access colorScheme={colorScheme} />} />
		// case '/help':
		// 	return <Route path="/help" render={() => <Help colorScheme={colorScheme} />} />
		default:
			return <App setColorScheme={setColorScheme} />;
	}

}

export default withRouter(AppWrapper);