import React, { useState } from 'react'
import { useHistory } from 'react-router-dom'
import { Row, Col, Form, Input, Select, Tag, Button, DatePicker, Space, Radio, Image } from 'antd'
import * as anticons from '@ant-design/icons'
import toast from 'react-hot-toast'

import TrainingService from '../../service/TrainingService'
import PostulantService from '../../service/PostulantService'
import CourseService from '../../service/CourseService'
import { getCatalogos, getDatosUsuario } from '../../utils/Utils'

const baseTraining = {
}

const { Option } = Select
const trainingService = new TrainingService()
const postulantService = new PostulantService()
const courseService = new CourseService()

export const Course = () => {

  const navigate = useHistory()
  const [isSaving, setIsSaving] = useState(false)
  const [postulant, setPostulant] = useState('')
  const [postulants, setPostulants] = useState([])
  const [courses, setCourses] = useState([])
  const [error, setError] = useState('')
  const [selectedModality, setSelectedModality] = useState(0)

  const renderPaisValor = e => <Option key={e.catValor} value={e.catId}>
    <>
      <img
        src={`https://flagcdn.com/24x18/${e.catDescripcion.toLowerCase()}.png`}
        srcSet={`https://flagcdn.com/48x36/${e.catDescripcion}.png 2x,https://flagcdn.com/72x54/${e.catDescripcion}.png 3x`}
        width="24"
        height="18"
        alt={e.catNombre}></img>
      &nbsp;{
        e.catNombre
      }
    </>
  </Option>

  const renderModality = ({ catId, catNombre, catValor }) =>
    <Radio.Button
      key={catId}
      value={catId}
      style={{
        height: '100%',
        margin: '0 20px 20px 0',
      }}
      onChange={e => setSelectedModality(catId)}
    >
      <Row>
        <Col style={{
          padding: '20px 20px',
          borderRadius: '15px',
        }}>
          <Image preview={false} src={`./../../assets/${catValor}${catId === selectedModality ? '_W' : ''}.png`} width={55} height={50} />
        </Col>
      </Row>
      {catNombre}
    </Radio.Button>

  const renderApplicants = ({ posEmail }) =>
    <Col key={posEmail}>
      <Tag key={posEmail} closeIcon={<anticons.CloseOutlined />} onClose={(e) => { removePostulant(posEmail) }}
        closable
        style={{
          padding: '5px 15px',
          borderRadius: '15px',
          fontSize: '1em'
        }}
      >
        {posEmail}
      </Tag>
    </Col>

  const addPostulant = () => {
    if (postulant === '') return;
    if (postulants.filter(p => p.posEmail === postulant).length > 0) {
      setPostulant('')
      return;
    }
    postulants.push(
      {
        posEmail: postulant
      }
    )
    setPostulant('')
  }

  const removePostulant = (email) => {
    const newPostulantsList = postulants.filter(p => p.posEmail !== email)
    setPostulants(newPostulantsList)
  }

  const save = async (values) => {
    const training = {
      ...values,
      idEstado: getCatalogos().trainingStatus.filter(s => s.catValor === 'DRAFT')[0].catId,
      idUsuario: getDatosUsuario().usuarioId
    }
    setIsSaving(true)
    try {
      const { data } = await trainingService.put({ navigate, training })
      if (data) {
        setError(null)
        // Asignar cursos
        const requests = []
        if (courses.length > 0) {
          courses.forEach(p => {
            requests.push(courseService.put({
              navigate, course: {
                "idEntrenamiento": data.listObject[0].entId,
                "idNombreCurso": p.catId
              }
            }))
          });
        }
        // Asignar postulantes
        if (postulants.length > 0) {
          postulants.forEach(p => {
            requests.push(postulantService.put({
              navigate, postulant: {
                "idEntrenamiento": data.listObject[0].entId,
                "posEmail": p.posEmail,
                "idInvitadoPor": getDatosUsuario().usuarioId
              }
            }))
          });
        }
        if (requests.length > 0) {
          Promise.all(requests)
            .then((results) => {
              toast.success(`Training ${data.listObject[0].entNombre} succesfully created`)
              navigate.push(`/trainingedit/${data.listObject[0].entId}`)
            }).catch(error => {
              const { response } = error
              const msgError = response.status === 409 ? response.data[0].messageText : response.data
              toast.error(`It was not possible to add the applicant: ${msgError}`)
            })
        } else {
          toast.success(`Training ${data.listObject[0].entNombre} succesfully created`)
          navigate.push(`/trainingedit/${data.listObject[0].entId}`)
        }
      } else {
        setError("An error occurred.")
      }
    } catch (error) {
      const { response } = error
      setError(response.status === 409 ? response.data[0].messageText : "An error occurred.")
    } finally {
      setIsSaving(false)
    }
  }

  return (
    <>
      <Form
        layout='vertical'
        initialValues={
          baseTraining
        }
        onFinish={save}
        disabled={isSaving}
      >
        <div className="grid">
          <div className="col-12">
            <div className="mgeCard">
              <div className="grid">
                <div className="col-4">
                  <Space direction='horizontal'>
                    <Button icon={<i className="fa-solid fa-chevron-left"></i>}
                      onClick={() => {
                        navigate.push('/training')
                      }}
                    ></Button>
                    <h3 style={{ padding: '0', margin: '0' }} className='mge-darkGreen'>
                      New Training
                    </h3>
                  </Space>
                </div>
                <div className="col-8" style={{ textAlign: 'right' }}>
                  <Space>
                    <Button
                      type='default'
                      htmlType='submit'
                      loading={isSaving}
                      disabled={isSaving}
                    >Save Draft</Button>
                    <Button
                      type='primary'
                      loading={isSaving}
                      disabled
                      onClick={() => {
                        toast.success('Training published')
                      }}
                    >Publish</Button>
                  </Space>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Row justify='center'>
          <Col xs={22} sm={22} md={20} lg={18} xl={18} xxl={16}>
            <Row gutter={[25, 25]}>
              <Col span={24}>
                <Form.Item
                  name='idPais'
                  label="Country"
                  rules={[
                    {
                      required: true,
                      message: 'Select country',
                    },
                  ]}
                >
                  <Select name='idPais'>
                    {
                      getCatalogos().paises.map(p => renderPaisValor(p))
                    }
                  </Select>
                </Form.Item>
              </Col>
              {/* <Col span={16}>
                <Form.Item
                  name='name'
                  label="Training name"
                  rules={[
                    {
                      required: true,
                      message: 'Complete training name',
                    },
                  ]}
                >
                  <Input />
                </Form.Item>
              </Col> */}
            </Row>
            <Row gutter={[25, 25]}>
              <Col span={24}>
                <Form.Item
                  name='idModalidad'
                  label="Modality"
                  rules={[
                    {
                      required: true,
                      message: 'Select modality',
                    },
                  ]}
                  style={{ width: '100%', textAlign: 'center' }}
                >
                  <Radio.Group buttonStyle='solid'>
                    {
                      getCatalogos().modalidades.map(m => renderModality(m))
                    }
                  </Radio.Group>
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={[25, 25]}>
              <Col span={12}>
                <Form.Item
                  name='entFechaIni'
                  label="Start Date"
                  rules={[
                    {
                      required: true,
                      message: 'Select start date',
                    },
                  ]}
                >
                  <DatePicker format={'YYYY-MM-DD'} style={{ width: '100%' }} />
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  name='entFechaFin'
                  label="End Date"
                  rules={[
                    {
                      required: true,
                      message: 'Select end date',
                    },
                  ]}
                >
                  <DatePicker format={'YYYY-MM-DD'} style={{ width: '100%' }} />
                </Form.Item>
              </Col>
            </Row>
          </Col>
        </Row>
      </Form>
      <Row justify='center'>
        <Col xs={22} sm={22} md={20} lg={18} xl={18} xxl={16}>
          <Row >
            <Form layout='vertical' style={{ width: '100%' }}>
              <Col span={24}>
                <Form.Item
                  name={['courses']}
                  label='Courses'
                  rules={[
                    {
                      required: true,
                      message: 'Select courses',
                    },
                  ]}
                >
                  <Select
                    style={{
                      width: '100%',
                      display: 'inline-block'
                    }}
                    mode='multiple'
                    fieldNames={{
                      label: 'catNombre',
                      value: 'catId'
                    }}
                    options={getCatalogos().cursos}
                    showSearch
                    optionFilterProp="catNombre"
                    onChange={(_, option) => {
                      setCourses(option)
                    }}
                  />
                </Form.Item>
              </Col>
            </Form>
          </Row>
        </Col>
      </Row>
      <Row justify='center'>
        <Col xs={22} sm={22} md={20} lg={18} xl={18} xxl={16}>
          <Row>
            <Form layout='vertical' style={{ width: '100%' }}>
              <Col span={24}>
                <Form.Item
                  name={['applicants']}
                  label='Applicants emails'
                  extra='Press enter to add email'
                  rules={[
                    {
                      required: true,
                      message: 'Complete applicants',
                    },
                  ]}
                >
                  <Input
                    style={{ width: '100%' }}
                    value={postulant}
                    onChange={(e) => {
                      setPostulant(e.target.value)
                    }}
                    onKeyUp={(e) => {
                      e.preventDefault()
                      if (e.keyCode === 13) {
                        addPostulant()
                      }
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={24}>
                <Row gutter={[15, 15]}>
                  {
                    postulants && postulants.map(a => renderApplicants(a))
                  }
                </Row>
              </Col>
            </Form>
          </Row>
        </Col>
      </Row>
    </>
  )
}
