import AxiosInstance from '../utils/AxiosInstance'
import AxiosInstanceBlob from '../utils/AxiosInstanceBlob'
import { downloadFinalReport, downloadInformByName, downloadWorkplan, getInformById, insertInform, updateInform } from '../utils/urls'

export class InformService {
    put = (props) => {
        const { navigate, inform } = props
        return AxiosInstance(navigate).put(insertInform(), inform,
            {
                headers: { "Content-Type": "multipart/form-data" }
            })
    }
    set = (props) => {
        const { navigate, inform } = props
        return AxiosInstance(navigate).patch(updateInform(), inform)
    }
    get = (props) => {
        const { navigate, inform } = props
        return AxiosInstance(navigate).patch(getInformById(), inform)
    }
    downloadByName = (props) => {
        const { navigate, inform } = props
        return AxiosInstance(navigate).post(downloadInformByName(), inform)
    }
    downloadWorkplan = (props) => {
        const { navigate } = props
        return AxiosInstanceBlob(navigate).get(downloadWorkplan())
    }
    downloadFinalReport = (props) => {
        const { navigate } = props
        return AxiosInstance(navigate).get(downloadFinalReport())
    }
}

export default InformService