import React, { useState, useEffect } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import { Button, Col, Row, Table, Space, Typography, Card, Form, Input, Spin, Image } from 'antd'

import TrainingService from '../../service/TrainingService'
const trainingService = new TrainingService()

const tableProps = {
    bordered: false,
    loading: false,
    title: '',
    showHeader: true,
    size: 'medium',
    pagination: {
        position: ['none', 'bottomRight']
    }
};

const Applicants = () => {
    const navigate = useHistory()
    const { id } = useParams()
    const [course, setCourse] = useState(null)
    const [load, setLoad] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [error, setError] = useState(null)

    const fetchData = async () => {
        setLoad(false)
        setIsLoading(true)
        const training = { entId: id }
        try {
            const { data } = await trainingService.getById({ navigate, training })
            if (data) {
                setError(null)
                setCourse(data.listObject[0])
            } else {
                setError("Hubo un error al obtener el entrenamiento")
            }
        } catch (error) {
            const { response } = error
            setError(response.status === 409 ? response.data[0].messageText : "No pudimos hacer la solicitud para obtener el entrenamiento")
        } finally {
            setIsLoading(false)
        }
    }

    useEffect(() => {
        if (load) {
            fetchData()
        }
    })

    return (
        <>
            {
                isLoading ?
                    <Row style={{ padding: '30px 10px' }}>
                        <Col span={24} style={{ textAlign: 'center' }}>
                            <Spin size='large' tip={
                                <Typography.Paragraph style={{ padding: '10px 0' }}>
                                    Loading...
                                </Typography.Paragraph>
                            } />
                        </Col>
                    </Row>
                    :
                    course && <>
                        <Row>
                            <Col span={24}>
                                <Card bordered={false}>
                                    <Row>
                                        <Col>
                                            <Space direction='horizontal'>
                                                <Button icon={<i className="fa-solid fa-chevron-left"></i>}
                                                    onClick={() => {
                                                        navigate.push('/training')
                                                    }}
                                                ></Button>
                                                <h3 style={{ padding: '0', margin: '0' }} className='mge-darkGreen'>
                                                    {course.entNombre}
                                                </h3>
                                            </Space>
                                        </Col>
                                    </Row>
                                    <Row
                                        gutter={[15, 0]}
                                        justify='space-around'
                                    >
                                        <Col style={{ padding: '10px 10px 0 10px' }}>
                                            <i className="fa-solid fa-laptop"></i>&nbsp;{course.entModalidad.catNombre}
                                        </Col>
                                        <Col style={{ padding: '10px 10px 0 10px' }}>
                                            <i className="fa-solid fa-calendar-days"></i>&nbsp;{course.entFechaIni.substring(0, 10)}-{course.entFechaFin.substring(0, 10)}
                                        </Col>
                                        <Col style={{ padding: '10px 10px 0 10px' }}>
                                            <i className="fa-solid fa-graduation-cap"></i>&nbsp;{course.numPostulantes}&nbsp;Applicants
                                        </Col>
                                        <Col style={{ padding: '5px 10px 0 10px' }}>
                                            <img
                                                src={`https://flagcdn.com/32x24/${course.entPais.catDescripcion.toLowerCase()}.png`}
                                                srcSet={`https://flagcdn.com/32x24/${course.entPais.catDescripcion}.png 2x,https://flagcdn.com/96x72/${course.entPais.catDescripcion}.png 3x`}
                                                width="32"
                                                height="24"
                                                alt={course.entPais.catNombre}></img>
                                        </Col>
                                    </Row>
                                </Card>
                            </Col>
                        </Row>
                        <Row>
                            <Col style={{ padding: '15px 0' }}>
                                <Form
                                    layout='inline'
                                >
                                    <Form.Item
                                        label='Search expression'
                                    >
                                        <Input style={{ width: '100%' }} />
                                    </Form.Item>
                                    <Form.Item>
                                        <Button type='default'>Search</Button>
                                    </Form.Item>
                                </Form>
                            </Col>
                        </Row>
                        {
                            course.postulanteList && <>
                                <Table
                                    {...tableProps}
                                    rowKey='posId'
                                    columns={[
                                        {
                                            title: '',
                                            dataIndex: 'posId',
                                            render: (e) => <img alt='' src='./assets/user.png' width={32} height={32} />
                                        },
                                        {
                                            title: 'Name',
                                            dataIndex: 'usuId',
                                            render: (_, { posEmail, usuId }) => usuId ? `${usuId.nombres} ${usuId.apellidos}` : posEmail
                                        },
                                        {
                                            title: 'Country',
                                            dataIndex: 'country',
                                            render: (_, { usuId }) => usuId ?
                                                <div style={{ textAlign: 'center' }}>
                                                    <img
                                                        src={`https://flagcdn.com/32x24/${usuId.pais.catDescripcion.toLowerCase()}.png`}
                                                        srcSet={`https://flagcdn.com/32x24/${usuId.pais.catDescripcion}.png 2x,https://flagcdn.com/96x72/${usuId.pais.catDescripcion}.png 3x`}
                                                        width="32"
                                                        height="24"
                                                        alt={usuId.pais.catNombre}></img>
                                                </div>
                                                : <></>,
                                            responsive: ['md', 'lg', 'xl', 'xxl']
                                        },
                                        {
                                            title: 'Attendance',
                                            dataIndex: 'attendance',
                                            render: (_, { asistenciaList }) => asistenciaList && asistenciaList.length > 0 ?
                                                <div style={{ textAlign: 'center' }}>
                                                    <img alt='' src={`./../../assets/${(asistenciaList[asistenciaList.length - 1].asiValor || 0) >= 80 ? 'accept.png' : 'cancel.png'}`} height={24} width={24} />
                                                </div> :
                                                <div style={{ textAlign: 'center' }}>
                                                    <Image src='./../../assets/minus.png' preview={false} width={24} height={24} />
                                                </div>,
                                            responsive: ['xl', 'xxl']
                                        },
                                        {
                                            title: 'Work Plan',
                                            dataIndex: 'workPlan',
                                            render: (_, { informeList }) =>
                                                <div style={{ textAlign: 'center' }}>
                                                    {
                                                        informeList && informeList.filter(w => w.infTipo.catValor === 'PLAN_TRABAJO').length > 0 ?
                                                            <Image src={`./../../assets/${informeList.filter(w => w.infTipo.catValor === 'PLAN_TRABAJO')[informeList.filter(w => w.infTipo.catValor === 'PLAN_TRABAJO').length - 1].infEstado.catValor === 'EST_INF_ENV' ? 'send.png' : informeList.filter(w => w.infTipo.catValor === 'PLAN_TRABAJO')[informeList.filter(w => w.infTipo.catValor === 'PLAN_TRABAJO').length - 1].infEstado.catValor === 'EST_INF_APR' ? 'accept.png' : informeList.filter(w => w.infTipo.catValor === 'PLAN_TRABAJO')[informeList.filter(w => w.infTipo.catValor === 'PLAN_TRABAJO').length - 1].infEstado.catValor === 'EST_INF_REC' ? 'cancel.png' : 'minus.png'}`} preview={false} width={24} height={24} />
                                                            :
                                                            <Image src='./../../assets/minus.png' preview={false} width={24} height={24} />
                                                    }
                                                </div>,
                                            responsive: ['xl', 'xxl']
                                        },
                                        {
                                            title: 'Final Report',
                                            dataIndex: 'finalReport',
                                            render: (_, { informeList }) =>
                                                <div style={{ textAlign: 'center' }}>
                                                    {
                                                        informeList && informeList.filter(w => w.infTipo.catValor === 'INFORME_FINAL').length > 0 ?
                                                            <Image src={`./../../assets/${informeList.filter(w => w.infTipo.catValor === 'INFORME_FINAL')[informeList.filter(w => w.infTipo.catValor === 'INFORME_FINAL').length - 1].infEstado.catValor === 'EST_INF_ENV' ? 'send.png' : informeList.filter(w => w.infTipo.catValor === 'INFORME_FINAL')[informeList.filter(w => w.infTipo.catValor === 'INFORME_FINAL').length - 1].infEstado.catValor === 'EST_INF_APR' ? 'accept.png' : informeList.filter(w => w.infTipo.catValor === 'INFORME_FINAL')[informeList.filter(w => w.infTipo.catValor === 'INFORME_FINAL').length - 1].infEstado.catValor === 'EST_INF_REC' ? 'cancel.png' : 'minus.png'}`} preview={false} width={24} height={24} />
                                                            :
                                                            <Image src='./../../assets/minus.png' preview={false} width={24} height={24} />
                                                    }
                                                </div>,
                                            responsive: ['lg', 'xl', 'xxl']
                                        },
                                        {
                                            title: 'Certificate',
                                            dataIndex: 'certificate',
                                            render: (_, { certificadoList }) => certificadoList && certificadoList.length > 0 ? <>Certificado</> : <div style={{ textAlign: 'center' }}>
                                                <Image src='./../../assets/minus.png' preview={false} width={24} height={24} />
                                            </div>,
                                            responsive: ['lg', 'xl', 'xxl']
                                        },
                                        {
                                            title: 'Actions',
                                            dataIndex: '',
                                            render: (_, record) => <div style={{ textAlign: 'center' }}>
                                                <Button type='primary' href={`/#/applicants/${record.posId}/${id}`}>View</Button>
                                            </div>
                                        }
                                    ]}
                                    dataSource={course.postulanteList}>
                                </Table>
                            </>
                        }
                    </>
            }
        </>
    )
}

export default Applicants