import axios from "axios"
import { getToken } from "./Utils"

// responseType: 'arraybuffer'
const headers = {
}

const AxiosInstanceBlob = (navigate) => {

    const axiosInstanceBlob = axios.create({
        headers
    })

    axiosInstanceBlob.interceptors.request.use(
        (config) => {
            const token = getToken()
            if (token === undefined || token === null || token === "") {
                console.log('Login request')
                // navigate.push('/login')
            }
            config.headers["Authorization"] = `Bearer ${token}`
            return config
        },
        (error) => {
            return Promise.reject(error)
        }
    );

    // Agregar una respuesta al interceptor
    axiosInstanceBlob.interceptors.response.use(
        (response) => {
            if (response.status === 401 || response.status === 403) {
                localStorage.removeItem('mgeToken')
                localStorage.removeItem('mgeDatosUsuario')
                // localStorage.removeItem('mgeCatalogos')
                // console.log('Login response OK 401')
                navigate.push('/login')
            }
            return response
        },
        (error) => {
            if (error && error.response) {
                const { response } = error
                if (response.status === 401 || response.status === 403) {
                    localStorage.removeItem('mgeToken')
                    localStorage.removeItem('mgeDatosUsuario')
                    // localStorage.removeItem('mgeCatalogos')
                    // console.log('Login response error')
                    navigate.push('/login')
                }
            }
            return Promise.reject(error)
        }
    )

    return axiosInstanceBlob

}

export default AxiosInstanceBlob