import React, { useEffect, useState } from 'react'
import { useHistory, useLocation } from 'react-router-dom'
import { Layout, Row, Col, Image, Form, Input, Select, DatePicker, Switch, Button, Typography, Tooltip, Space, Spin, Result } from 'antd'
import * as anticons from '@ant-design/icons'
import PasswordChecklist from 'react-password-checklist'
import md5 from 'md5'
import { getCatalogos } from '../utils/Utils'

import CatalogoService from '../service/CatalogoService'
import UsuarioService from '../service/UsuarioService'
import PostulantService from '../service/PostulantService'
import toast from 'react-hot-toast'

const { Option } = Select
const usuarioService = new UsuarioService()
const postulantService = new PostulantService()
const catalogoService = new CatalogoService()

const renderPaisValor = e => <Option key={e.catValor} value={e.catId}>
    <>
        <img
            src={`https://flagcdn.com/24x18/${e.catDescripcion.toLowerCase()}.png`}
            srcSet={`https://flagcdn.com/48x36/${e.catDescripcion}.png 2x,https://flagcdn.com/72x54/${e.catDescripcion}.png 3x`}
            width="24"
            height="18"
            alt={e.catNombre}></img>
        &nbsp;{
            e.catNombre
        }
    </>
</Option>

const renderPaisCode = e => <Option key={e.catValor} value={e.catId}>
    <>
        +({e.catValor})&nbsp;
        <img
            src={`https://flagcdn.com/24x18/${e.catDescripcion.toLowerCase()}.png`}
            srcSet={`https://flagcdn.com/48x36/${e.catDescripcion}.png 2x,https://flagcdn.com/72x54/${e.catDescripcion}.png 3x`}
            width="24"
            height="18"
            alt={e.catDescripcion}></img>
    </>
</Option>

const renderError = (e) => <div key={e.messageCode}>{e.messageText}</div>

const Signup = () => {
    const { search } = useLocation()
    const { id, uuid } = JSON.parse('{"' + search.substring(1, search.length).replace(/&/g, '","').replace(/=/g, '":"') + '"}', function (key, value) { return key === "" ? value : decodeURIComponent(value) })

    const navigate = useHistory()
    const [load, setLoad] = useState(false)
    const [loadCatalogos, setLoadCatalogos] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [isRegister, setIsRegister] = useState(false)
    const [password, setPassword] = useState("")
    const [passwordConfirm, setPasswordConfirm] = useState("")
    const [currentlyWorking, setCurrentlyWorking] = useState(true)
    const [validForm, setValidForm] = useState(false)
    const [error, setError] = useState('')
    const [validPostulant, setValidPostulant] = useState()
    const [form] = Form.useForm()


    const validate = async () => {
        setLoad(false)
        setIsLoading(true)
        const postulant = {
            posId: id,
            posUuid: uuid
        }
        try {
            const { data } = await postulantService.validate({ postulant })
            if (data) {
                const validPostulantInfo = data.listObject[0]
                const { posEmail, posInvitadoPor, posId, posEstado } = validPostulantInfo
                const postulantInfo = { email: posEmail, invitadopor: posInvitadoPor.usuId, idPostulante: posId, posEstado }
                setValidPostulant(postulantInfo)
            }
        } catch (error) {
            const { response } = error
            setError(response.status === 409 ? response.data[0].messageText : "An error occurred.")
        } finally {
            setIsLoading(false)
        }
    }

    const fetchCatalogos = async () => {
        setLoadCatalogos(false)
        setIsLoading(true)
        // Get Catalogos
        const requests = []
        requests.push(catalogoService.getPaises())
        requests.push(catalogoService.getCursos())
        requests.push(catalogoService.getModalidad())
        requests.push(catalogoService.getGeneros())
        requests.push(catalogoService.getTrainingStatus())
        requests.push(catalogoService.getReportType())
        requests.push(catalogoService.getReportStatus())
        let mgeCatalogos = []
        Promise.all(requests)
            .then((results) => {
                if (results.length > 0) {
                    mgeCatalogos =
                    {
                        paises: results[0].data.listObject[0],
                        cursos: results[1].data.listObject[0],
                        modalidades: results[2].data.listObject[0],
                        generos: results[3].data.listObject[0],
                        trainingStatus: results[4].data.listObject[0],
                        reportType: results[5].data.listObject[0],
                        reportStatus: results[6].data.listObject[0]
                    }
                    validate()
                }
                localStorage.setItem("mgeCatalogos", JSON.stringify(mgeCatalogos))
            })
            .catch(error => {
                const { response } = error
                const msgError = response.status === 409 ? response.data[0].messageText : response.data
                setError(msgError)
                setIsLoading(false)
            })
    }

    const registrar = (values) => {
        const usuario = {
            ...values,
            password: md5(values.password),
            passwordConfirm: md5(values.passwordConfirm),
            trabaja: currentlyWorking,
            perId: 5,
            ...validPostulant
        }
        setIsRegister(true)
        usuarioService
            .put({ usuario })
            .then(response => {
                // setIsRegister(false)
                const { data } = response
                console.log(data)
                if (data && data.listObject.length > 0 && data.listObject[0].usuId) {
                    toast.success('Your account has been created successfully. Please log in.')
                    setTimeout(() => {
                        navigate.push('/login')
                    }, 3000);
                }
            })
            .catch(error => {
                setIsRegister(false)
                const { status, data } = error.response
                switch (status) {
                    case 409:
                        toast.error(data.map(e => renderError(e)))
                        break
                    case 500:
                        toast.error(data)
                        break
                    default:
                        break
                }
            })
    }

    useEffect(() => {
        if (loadCatalogos) {
            fetchCatalogos()
        }
    })

    return (
        <>
            <Row>
                <Col span={24}>
                    <div className="notification" style={{
                        borderRadius: '0px',
                        textAlign: 'center'
                    }}>
                        <Image alt='' src='./../assets/logo.png' preview={false} />
                    </div>
                </Col>
            </Row>

            <Layout style={{
                padding: '40px 20px',
                backgroundColor: '#F9F9F9'
            }}>
                <div className='mgeBG' style={{ opacity: '0.3', height: '100vh', position: 'fixed', width: '100%', bottom: '0', right: '0' }}>&nbsp;</div>
                {
                    isLoading ?
                        <Row style={{ padding: '30px 10px' }}>
                            <Col span={24} style={{ textAlign: 'center' }}>
                                <Spin size='large' tip={
                                    <Typography.Paragraph style={{ padding: '10px 0' }}>
                                        Loading...
                                    </Typography.Paragraph>
                                } />
                            </Col>
                        </Row>
                        :
                        validPostulant &&
                        <>
                            {
                                validPostulant.posEstado.catValor === 'EST_INV' ? <>
                                    <Row>
                                        <Col span={24} style={{ padding: '0 0 30px 0' }}>
                                            <Typography.Paragraph style={{ textAlign: 'center' }}>
                                                <Typography.Text className="mge-darkGreen"
                                                    style={{
                                                        display: 'block',
                                                        fontSize: '2em',
                                                        fontWeight: 'bold'
                                                    }}
                                                >
                                                    Welcome to My Green Enterprise
                                                </Typography.Text>
                                                <Typography.Text className="mge-green"
                                                    style={{
                                                        display: 'block',
                                                        fontSize: '1.5em'
                                                    }}
                                                >
                                                    Complete the following information to active your account
                                                </Typography.Text>
                                            </Typography.Paragraph>
                                        </Col>
                                    </Row>
                                    <Row justify='center'>
                                        <Col xs={24} sm={22} md={20} lg={16} xl={14} xxl={12}>
                                            <Form
                                                layout={'vertical'}
                                                form={form}
                                                initialValues={
                                                    validPostulant
                                                }
                                                onFinish={registrar}
                                                disabled={isRegister}
                                            >
                                                <Row gutter={[10, 0]}>
                                                    <Col xs={24} sm={8} md={8} lg={8} xl={8} xxl={8}>
                                                        <Row>
                                                            <Col span={24}>
                                                                <Typography.Paragraph style={{ textAlign: 'center' }}>
                                                                    Upload your profile picture
                                                                </Typography.Paragraph>
                                                            </Col>
                                                            <Col span={24}>
                                                                <Typography.Paragraph style={{ textAlign: 'center' }}>
                                                                    <Image src='./../assets/user.png' preview={false} />
                                                                </Typography.Paragraph>
                                                            </Col>
                                                            <Col span={24} style={{ textAlign: 'center' }}>
                                                                <Form.Item>
                                                                    <Button type='primary'>Upload photo</Button>
                                                                </Form.Item>
                                                            </Col>
                                                        </Row>
                                                    </Col>
                                                    <Col xs={24} sm={16} md={16} lg={16} xl={16} xxl={16}>
                                                        <Row gutter={[15, 0]}>
                                                            <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                                <Form.Item
                                                                    name='nombres'
                                                                    label="First Name"
                                                                    rules={[
                                                                        {
                                                                            required: true,
                                                                            message: 'Complete first Name',
                                                                        },
                                                                    ]}
                                                                >
                                                                    <Input />
                                                                </Form.Item>
                                                            </Col>
                                                            <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                                <Form.Item
                                                                    name='apellidos'
                                                                    label="Last Name"
                                                                    rules={[
                                                                        {
                                                                            required: true,
                                                                            message: 'Complete last Name',
                                                                        },
                                                                    ]}
                                                                >
                                                                    <Input />
                                                                </Form.Item>
                                                            </Col>
                                                            <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                                <Form.Item
                                                                    name={['sexoId']}
                                                                    label='Sex'
                                                                    rules={[
                                                                        {
                                                                            required: true,
                                                                            message: 'Select sex',
                                                                        },
                                                                    ]}
                                                                >
                                                                    <Select
                                                                        style={{
                                                                            width: '100%',
                                                                            display: 'inline-block'
                                                                        }}
                                                                        fieldNames={{
                                                                            label: 'catNombre',
                                                                            value: 'catId'
                                                                        }}
                                                                        showSearch
                                                                        options={getCatalogos().generos}
                                                                        optionFilterProp="catNombre"
                                                                    />
                                                                </Form.Item>
                                                            </Col>
                                                            <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                                <Form.Item
                                                                    name='fechanac'
                                                                    label="Date of birth"
                                                                    rules={[
                                                                        {
                                                                            required: true,
                                                                            message: 'Select date of birth',
                                                                        },
                                                                    ]}
                                                                >
                                                                    <DatePicker format={'YYYY-MM-DD'} style={{ width: '100%' }} />
                                                                </Form.Item>
                                                            </Col>
                                                        </Row>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                                                        <Form.Item
                                                            name='documento'
                                                            label={
                                                                <Space direction='horizontal'>
                                                                    <>ID Number</>
                                                                    <Tooltip
                                                                        title={
                                                                            <>
                                                                                This is the unique identification number issued by the country of residence.
                                                                            </>
                                                                        }
                                                                        placement='right'
                                                                    >
                                                                        <anticons.InfoCircleFilled />
                                                                    </Tooltip>
                                                                </Space>
                                                            }
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Complete ID Number',
                                                                },
                                                            ]}
                                                        >
                                                            <Input />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                                                        <Form.Item
                                                            name='paisId'
                                                            label="Country"
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Select country',
                                                                },
                                                            ]}
                                                        >
                                                            <Select name='paisId'>
                                                                {
                                                                    getCatalogos().paises.map(p => renderPaisValor(p))
                                                                }
                                                            </Select>
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                                                        <Form.Item
                                                            name='ciudad'
                                                            label="City"
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Complete city',
                                                                },
                                                            ]}
                                                        >
                                                            <Input />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                                                        <Form.Item
                                                            name='codigotel'
                                                            label="Country Code"
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Select country code',
                                                                },
                                                            ]}
                                                        >
                                                            <Select name='codigotel'>
                                                                {
                                                                    getCatalogos().paises.map(p => renderPaisCode(p))
                                                                }
                                                            </Select>
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                                                        <Form.Item
                                                            name='celular'
                                                            label="Phone Number"
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Complete phone number',
                                                                },
                                                            ]}
                                                        >
                                                            <Input />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                                                        <Form.Item
                                                            name='lugartrabajo'
                                                            label={
                                                                <Space>
                                                                    Are you currently working?
                                                                    <Switch defaultChecked onChange={(c) => { setCurrentlyWorking(c) }} />
                                                                </Space>
                                                            }
                                                            rules={[
                                                                {
                                                                    required: currentlyWorking,
                                                                    message: 'Are you currently working?',
                                                                },
                                                            ]}
                                                        >
                                                            <Input name='lugartrabajo' disabled={!currentlyWorking} />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name='email'
                                                            label="Email"
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Complete email',
                                                                },
                                                            ]}
                                                        >
                                                            <Input disabled />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name='email2'
                                                            label="Additional Email"
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Complete additional email',
                                                                },
                                                            ]}
                                                        >
                                                            <Input />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item label="Create your password"
                                                            name='password'
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Create your password',
                                                                },
                                                            ]}
                                                        >
                                                            <Input.Password id="password" name="password" onChange={e => setPassword(e.target.value)} />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item label="Confirm your password"
                                                            name='passwordConfirm'
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Confirm your password',
                                                                },
                                                            ]}
                                                        >
                                                            <Input.Password id="passwordConfirm" name="passwordConfirm" onChange={e => setPasswordConfirm(e.target.value)} />
                                                        </Form.Item>
                                                    </Col>
                                                    {
                                                        validForm === false &&
                                                        <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}
                                                        >
                                                            <PasswordChecklist
                                                                rules={["minLength", "specialChar", "number", "capital", "match"]}
                                                                minLength={8}
                                                                value={password}
                                                                valueAgain={passwordConfirm}
                                                                messages={{
                                                                    minLength: "Password has more than 8 characters.",
                                                                    specialChar: "Password has special characters.",
                                                                    number: "Password has a number.",
                                                                    capital: "Password has a capital letter.",
                                                                    match: "Passwords match.",
                                                                }}
                                                                onChange={(isValid) => { setValidForm(isValid) }}
                                                                style={{
                                                                    padding: '0 0 20px 0'
                                                                }}
                                                            />
                                                        </Col>
                                                    }
                                                    <Col span={24} style={{ textAlign: 'center' }}>
                                                        <Form.Item>
                                                            <Button type='primary' htmlType='submit' loading={isLoading} disabled={isLoading} block>Register</Button>
                                                        </Form.Item>
                                                    </Col>
                                                </Row>
                                            </Form>
                                        </Col>
                                    </Row>
                                </>
                                    :
                                    <Result
                                        title="Your account has been previously created"
                                        extra={
                                            <Button type="primary" key="console"
                                                onClick={() => {
                                                    navigate.push('/login')
                                                }}
                                            >
                                                Log in
                                            </Button>
                                        }
                                    />
                            }
                        </>
                }
            </Layout>
        </>
    )
}

export default Signup