import React, { useState } from "react"
import { Link, useHistory } from "react-router-dom"
import { Input, Form, Button, Row, Col, Divider, Image, Typography, Layout, Spin, Result, Alert } from 'antd'
import AlertWait from "./AlertWait"
import Logo from "../components/Logo"
import CuentaService from "../service/CuentaService"
import toast from "react-hot-toast"

const cuentaService = new CuentaService()

const RecuperarPassword = () => {
    const navigate = useHistory()
    const [isLoading, setIsLoading] = useState(false)
    const [validPostulant, setValidPostulant] = useState({
        posEstado: {
            catValor: 'EST_INV'
        }
    })
    const [okMsg, setOkMsg] = useState(null)
    const [error, setError] = useState(null)
    const [form] = Form.useForm();

    const recuperar = (values) => {
        setError(null)
        setOkMsg(null)
        setIsLoading(true)
        const datos = { ...values }
        cuentaService
            .recoveryPassword({ datos })
            .then((response) => {
                setIsLoading(false)
                setOkMsg('Your password was sent to the indicated email. Check your inbox.')
                // toast.success('Your password was sent to the indicated email. Check your inbox.')
            })
            .catch((error) => {
                setIsLoading(false)
                const { response } = error
                if (response.status === 500) {
                    setError(response.statusText)
                    // toast.error(response.statusText)
                } else if (response.status === 404) {
                    setError("The requested resource is not available or cannot be found.")
                    // toast.error('The requested resource is not available or cannot be found')
                } else {
                    setError(response.data[0].messageText)
                    // toast.error(response.data[0].messageText)
                }
            })
    }

    return (
        <>
            <Row>
                <Col span={24}>
                    <div className="notification" style={{
                        borderRadius: '0px',
                        textAlign: 'center'
                    }}>
                        <Image alt='' src='./../assets/logo.png' preview={false} />
                    </div>
                </Col>
            </Row>

            <Layout style={{
                padding: '40px 20px',
                backgroundColor: '#F9F9F9'
            }}>
                <div className='mgeBG' style={{ opacity: '0.3', height: '100vh', position: 'fixed', width: '100%', bottom: '0', right: '0' }}>&nbsp;</div>
                {
                    validPostulant &&
                    <>
                        {
                            validPostulant.posEstado.catValor === 'EST_INV' ? <>
                                <Row justify='center'>
                                    <Col xs={24} sm={18} md={16} lg={12} xl={8} xxl={6}>
                                        <Divider><span className="mge-darkGreen" style={{ textTransform: 'uppercase', letterSpacing: '1px' }}>Recover password</span></Divider>
                                        <Form
                                            layout={'vertical'}
                                            form={form}
                                            onFinish={recuperar}
                                        >
                                            <Form.Item label="Email"
                                                name='email'
                                                rules={[
                                                    {
                                                        type: 'email',
                                                        message: 'Email is not valid'
                                                    },
                                                    {
                                                        required: true,
                                                        message: 'Complete your email',
                                                    },
                                                ]}
                                            >
                                                <Input id="email" name="email" label='Email' type='text' />
                                            </Form.Item>

                                            {
                                                error &&
                                                <Alert
                                                    message=''
                                                    description={error}
                                                    type='warning'
                                                    showIcon
                                                    closable
                                                    banner
                                                />
                                            }

                                            {
                                                okMsg &&
                                                <Alert
                                                    message=''
                                                    description={okMsg}
                                                    type='success'
                                                    showIcon
                                                    closable
                                                    banner
                                                />
                                            }

                                            <div style={{
                                                paddingTop: '20px'
                                            }}>
                                                <Form.Item>
                                                    <Button
                                                        type='primary'
                                                        htmlType='submit'
                                                        loading={isLoading}
                                                        disabled={isLoading}
                                                        block
                                                    >Recover Password</Button>
                                                </Form.Item>
                                            </div>
                                        </Form>
                                    </Col>
                                </Row>
                            </>
                                :
                                <Result
                                    title="Your account has been previously created"
                                    extra={
                                        <Button type="primary" key="console"
                                            onClick={() => {
                                                navigate.push('/login')
                                            }}
                                        >
                                            Log in
                                        </Button>
                                    }
                                />
                        }
                    </>
                }
            </Layout>
        </>
    )
}

export default RecuperarPassword